.search-item {
  display: flex;
  margin-bottom: 2em;
  background: #fff;
  overflow: hidden;
  @include box-shadow(0px, 0px, 5px, 1px,  rgba(0,0,0,0.1), false);
  @include medium {
    flex-direction: column; }
  &-info {
    display: flex;
    flex-direction: column;
    width: 100%;
    h1 {
      padding-left: 2rem;
      margin-bottom: 0.5rem; }
    .result-info {
      padding: 0 2rem 1rem 2rem;
      flex: 1; } } }

.poster {
  &-link {
    display: flex;
    min-width: 20rem;
    max-width: 20rem;
    min-height: 30rem;
    @include medium {
      max-width: 100%; } }
  &-image {
    width: 100%;
    object-fit: cover; } }

.read-more-btn {
  border-radius: 0;
  background-color: #cfd8dc;
  color: #102027;
  &:hover {
    color: #fff; } }
